html {
  box-sizing: border-box;
}

body {
  background-color: #f0f2f5;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.link {
  text-decoration: none;
}

.button {
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  display: inline-block;
}

.invisible {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
